body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.field {
  border-color: lightgray;
  margin-bottom: 1rem;
  border-width: 1px;
  padding: 0 1rem;
}

.hidden {
  display: none;
}

.label {
  border: lightgray;
  color: rgba(30, 30, 30, 0.7);
  border-width: 1px;
  padding-bottom: 1rem;
}

.thumbnail {
  height: 50px;
  width: 50px;
  border-radius: 8px;
}

.dropdown {
  position: fixed;
  inset: 0;
  width: 300px;
  height: 300px;
}

.disabled {
  color: lightgray;
}

.table-spacing tr {
  padding: 0;
  text-align: center;
}

.tr input {
  padding: 0;
  width: 80px;
  color: gray;
}

.editable {
  border: 1px solid lightgray;
  padding: 1rem;
}
